import React from 'react';
import { styled } from '@mui/material';
import DrawerMenu from 'components/DrawerMenu';
import { Outlet, useSearchParams } from 'react-router-dom';
import { useAppDispatch } from 'hooks/redux';
import { updateInviter } from 'store/features/applicationSlice';

const drawerWidth = 220;

const Container = styled('div')(({ theme }) => ({
  display: 'flex',
  height: '100vh',
  width: '100vw',
  [theme.breakpoints.down('sm')]: {
    height: 'calc(100%)',
    width: 'calc(100%)',
  },
}));

const Main = styled('main', { shouldForwardProp: (prop) => prop !== 'open' })<{
  open?: boolean;
}>(({ theme, open }) => ({
  position: 'relative',
  flex: 1,
  transition: theme.transitions.create('margin', {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  marginLeft: `-${drawerWidth}px`,
  ...(open && {
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginLeft: 0,
  }),
}));

const BaseLayout = () => {
  const [open] = React.useState(true);
  const [searchParams] = useSearchParams();
  const inviter = searchParams.get('referrer');
  const dispatch = useAppDispatch();

  React.useEffect(() => {
    if (inviter) {
      dispatch(updateInviter({ inviter: inviter }));
    } else {
      dispatch(updateInviter({ inviter: '' }));
    }
  }, [dispatch, inviter]);

  return (
    <Container>
      <DrawerMenu open={open} drawerWidth={drawerWidth} />
      <Main open={open}>
        <Outlet />
      </Main>
    </Container>
  );
};

export default BaseLayout;
